<template>
  <div class="part-four">
    <h3 class="text-center text-top">所获奖励</h3>
    <p class="text-small"><span class="text-small-pot">2017.04</span>获“校级学生会先进个人”称号。</p>
    <p class="text-small"><span class="text-small-pot">2017.05</span>获“校级优秀共青团员”称号。</p>
    <p class="text-small"><span class="text-small-pot">2017.10</span>获“校级二等奖学金”一次</p>
    <p class="text-small"><span class="text-small-pot">2018.10</span>获“校级二等奖学金”一次</p>
    <p class="text-small"><span class="text-small-pot">·</span>CET-4通过</p>
    <p class="text-small"><span class="text-small-pot">·</span>CET-6通过，熟悉阅读英文文档</p>
    
  </div>
</template>

<script>
export default {
  name: "part-four",
  data() {
    return {
      
    };
  },
  
};
</script>

<style scoped>
@-webkit-keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}
@keyframes masked-animation {
    0% {
            background-position: 0 0
        }
    to {
            background-position: -100% 0
        }
}



.part-four {
  width: 100%;
  height: 450px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
  text-align: left;
}

.text-center {
    text-align: center;
    margin-bottom: 30px;
    color: hsla(0, 0%, 100%, .65);
    font-weight: 800;
    background-image: -webkit-linear-gradient(left, #00bfff, #207ffc 25%, #00eeff 50%, #0066ff 75%, #00ffdd);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-background-size: 200% 100%;
    background-size: 200% 100%;
    -webkit-animation: masked-animation 2s infinite linear;
    animation: masked-animation 2s infinite linear;

}
.text-center:hover {
     -webkit-animation: masked-animation 0.5s infinite linear;
     animation: masked-animation 0.5s infinite linear;
     cursor: pointer;
}
.text-small {
    line-height: 30px;
    font-size: 0.9em;
    color: rgb(253, 239, 239);
}
.text-small-pot {
    color: deepskyblue;
}

</style>


